import "../styles/Navbar.css";
import React, { useRef } from 'react';
import logo from "../images/icons8-slice-50.png";
import navbtn from "../images/icons8-menu.svg";
import navbtn2 from "../images/icons8-menu-51.png"

function Navbar() {
    const reff = useRef();
    function handleOver ()
    {
        if(reff.current.src === navbtn2)
            reff.current.src = navbtn;
        else
            reff.current.src = navbtn2;
    }
  return (
    <div className="navbar">
        <img src={logo} alt="logo"  id="logo"/>
        <div className="nav-button">
            <img src={navbtn} onMouseOver={handleOver} onMouseOut={handleOver}  ref={reff}alt="button" />
        </div>
    </div>
  )
}

export default Navbar
